@import url('https://fonts.googleapis.com/css2?family=Courier+Prime:wght@700&family=Roboto+Mono:wght@600;700&display=swap');

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    padding: 20px;
    font-family: "Courier Prime";
    font-weight: 700;
    background-color: rgba(27, 21, 21, 0.5);
    backdrop-filter: blur(50px);
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    position: relative;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

nav h1 {
    margin: 0;
}

nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

nav li {
    margin-left: 10px;
}

.fa-brands{
    font-size: 30px;
    text-align: left;
    padding-left: 0;
    color: #a9a2a2;
    transition: all 0.3s;
}

.fa-brands:hover{
    color: orange;
    
    
}

.fa-github{
    margin-right: 10px;
    margin-left: 10px;
}

.fa-linkedin{
    margin-right: 10px;
    margin-left: 10px;
}

.fa-instagram{
    margin-right: 10px;
    margin-left: 10px;
}

.dropdown-container {
    position: relative;
}

.dropdown-toggle {
    position: relative;
    z-index: 1;
    background-color: #313030;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
    border: none;
    outline: none;
    cursor: pointer;
    color: orange;
    font-family: "Courier Prime";
    width: 150px;
    height: 40px;
    font-size: 20px;
    border-radius: 10px;
    transition: all 300ms;
}

.dropdown-toggle:hover{
    background-color: #484646;
}   

.dropdown-menu {
    position: absolute;
    top: calc(70% + 0.5rem);
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.5rem 1rem;
    background-color: #313030;
    border-radius: 0.5rem;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
    margin-right: 20px;
    width: 118px;
}



.dropdown-menu a {
    display: block;
    margin-bottom: 0.5rem;
    text-decoration: none;
    font-size: 1.2rem;
    padding-top: 10px;
}

.dropdown-menu i:hover{
    color: orange;
}

