@import url('https://fonts.googleapis.com/css2?family=Courier+Prime:wght@700&family=Roboto+Mono:wght@600;700&display=swap');

.project-list{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.project-card {
    text-align: center;
    border-radius: 20px;
    padding: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease-in-out;
    width: 250px;
    height: 290px;
    margin: 20px;
    background: #313030;
    transition: all 500ms;
}


.project-card:hover {
    transform: translateY(-4px);
    background: #484646;
    
}

img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    margin-bottom: 16px;
}

h2 {
    font-size: 24px;
    margin-bottom: 8px;
    text-align: center;
}

p {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 16px;
}
  
ul {
    font-family: "Courier Prime";
    text-align: left;
    list-style-type: none;
    padding-left: 0;
}

.project-text {
    color: orange;
}

