body {
    background: linear-gradient(0deg, #1b1b1c, #242425, rgba(45, 45, 45, 1));
    background-repeat: no-repeat;
    color: white;
}

/* Typing Effect  */
.typing {
    color: #fff;
    padding: 5px;
    font-family: "Courier Prime";
    text-align: center;
    font-size: 30px;
}

.typing-name {
    color: orange;
}

.blinker {
    animation: blink-animation 1s steps(5, start) infinite;
    color: white;
}

@keyframes blink-animation {
    to {
        visibility: hidden;
    }
}

.typing {
    overflow: hidden;
    white-space: nowrap;
    margin: 0 auto;
    animation: typing 3s steps(45, end), blink-caret 0.75s step-end infinite;
}

@keyframes typing {
    from {
        width: 0;
    }
    to {
        width: 100%;
}
}

@keyframes blink-caret {
    from,
    to {
        border-color: transparent;
    }
    50% {
        border-color: orange;
    }
}

.title {
    color: orange;
    font-family: "Courier Prime";
    font-size: 45px;
    margin-left: 25px;
}

.resume {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 35px;
    position: relative;
}

.resumeButton {
    background-color: #313030;
    border-radius: 15px;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
    width: 120px;
    height: 65px;
    transition: all 300ms;
    position: absolute;
    top: 0;
    border: none;
}

.resumeButton p {
    color: orange;
    font-family: "Courier Prime";
    text-align: center;
}

.resumeButton:hover {
    border-radius: 15px;
    background-color: #484646;
    color: #313030;
    cursor: pointer;

}

.resumeButtonText {
    color: black;
    text-decoration: none;
    font-family: "Courier Prime";
    font-size: 20px;
}

.iframe-container{
    position: block;
    width: 100%;
    height: calc(100% - 70px);
    margin-top: 80px;
    border: none;
    background-color: #f2f2f2;
}
